export default {
  name: "SignIn",
  //
  data() {
    return {
      // 账号密码
      user: { email: "", password: "" },
      // 登录失败原因提示
      errorVisible: false,
      //
      signInLoading: false,
      //
      errorList: {},
    };
  },
  //
  methods: {
    //
    handleSignIn() {
      this.signInLoading = true;
      this.errorVisible = false;
      //
      this.$axios
        .post("/users/login", {
          user: this.user,
        })
        .then(({ data }) => {
          // 保存用户信息
          const { token } = data.user;
          localStorage.setItem("jwtToken", token);

          // 跳转至首页
          this.$router.push("/");
        })
        .catch(({ errors }) => {
          this.errorList = errors;
          this.errorVisible = true;
        })
        .finally(() => {
          this.signInLoading = false;
        });
    },
  },
};
